$sub-nav-bg-color: white;
$sub-nav-link-color: rgba($base-font-color, 0.7);
$sub-nav-link-color-hover: $dark-gray;

.header-nav-wrap {
  display: none;

  @media ($large-screen-up) {
    align-items: flex-end;
    display: flex;
    flex-direction: column;
  }
}

.header-nav-wrap .menu {
  clear: both;
  font-family: $base-font-family;
  font-size: $base-font-size;
  line-height: 1;
  margin: 0;
  padding: 0;
  width: 100%;

  .menu-item {
    display: inline-block;
    font-size: $base-font-size;
    margin-bottom: 0;
    position: relative;
    text-align: left;

    &:after {
      content: "";
      height: rem(20);
      left: 0;
      position: absolute;
      top: 100%;
      width: 100%;
    }

    > a {
      color: #bbb;
      color: rgba(255,255,255,0.7);
      display: block;
      padding: rem(16) 0.4rem;
      font-weight: 500;

      @media ($xl-screen-up) {
        padding: rem(16) 0.8rem;
      }
    }

    a:hover,
    a:focus,
    &.current-menu-item > a,
    &.current-menu-ancestor > a,
    &.current-menu-parent > a,
    &.menu-item-has-children.menu-item-hover > a {
      color: white;
    }

    &:hover,
    &:focus {
      > .sub-menu {
        opacity: 1;
        top: 100%;
        visibility: visible;
      }
    }

    &:last-child > a,
    &.last > a {
      padding-right: 0;
    }

    .sub-menu {
      background: $sub-nav-bg-color;
      margin: rem(10) 0 0 rem(-15);
      opacity: 0;
      padding: rem(30) rem(30);
      position: absolute;
      top: 250%;
      transition: all $medium-duration $base-timing;
      visibility: hidden;
      width: rem(200);
      z-index: 100;

      li {
        display: block;
        margin: 0;

        &:after {
          display: none;
        }

        a {
          color: $sub-nav-link-color;
          font-size: rem(14);
          padding: 0 0 rem(16) 0;
          position: relative;

          &:after {
            display: none;
          }

          &:hover {
            color: $sub-nav-link-color-hover;
          }
        }

        &.current-menu-item > a,
        &.current-menu-item > a:hover,
        &.current-menu-item > a:focus {
          color: $sub-nav-link-color-hover;
        }

        &:last-child a {
          padding: 0;
        }
      }

      .sub-menu {
        margin: rem(-41) 0 0 rem(199);
      }

      &.edge {
        left: auto;
        right: 0;
      }
    }

    &.nav-button {
      a {
        background: white;
        color: $dark-gray !important;
        border: 2px solid transparent;
        display: inline-block;
        font-family: $base-font-family;
        font-size: rem(14);
        padding: rem(10) rem(18);
        font-weight: 500;
        line-height: 1;
        margin-left: rem(16);

        &:hover {
          background-color: transparent;
          border-color: white;
          color: white !important;
        }
      }
    }
  }
}

.secondary-nav-wrap {
  align-items: center;
  display: flex;
  gap: rem(24);

  .menu {
    font-size: rem(12);
    a {
      color: $medium-gray;
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }
  }

  .switcher.notranslate .option {
    position: absolute;
  }
}
